import React from 'react';
import logo from '../assets/BUTC_Logo.svg';


class Homepage extends React.Component {

render() {
    const date = new Date()
    const year= date.getFullYear();
return(
    <div>
        <h1>Welcome to BUTC {year}</h1>
        <p/>
        <img src={logo} className="butc_logo" alt="logo" />
        <p />
        <p>Please note these pages automatically refresh every 5 seconds</p>

    </div>
)
}

}
export default Homepage;