import React from 'react';
import { Menu, Dropdown } from 'semantic-ui-react';
import logo from '../../assets/BUTC_Logo.svg';
import { NavLink, Switch, Route } from 'react-router-dom';
import UniversityDashboard from '../universities/UniversityDashboard';
import TargetListDashboard from '../TargetLists/TargetListDashboard';
import UniversityScores from '../seedingScoring/scoringViews/universityScores';
import IndividualScores from '../seedingScoring/scoringViews/individualScores';
import H2HTargetsR1A from '../H2H//terget_lists/h2hTargetsR1A';
import H2HTargetsR1B from '../H2H//terget_lists/h2hTargetsR1B';
import H2HTargetsR2 from '../H2H//terget_lists/h2hTargetsR2';
import H2HTargetsQF from '../H2H//terget_lists/h2hTargetsQF';
import H2HTargetsSF from '../H2H//terget_lists/h2hTargetsSF';
import H2HTargetsMB from '../H2H//terget_lists/h2hTargetsMB';
import H2HTargetsMG from '../H2H//terget_lists/h2hTargetsMG';
import H2hScoreViewR1A from '../H2H/score_views/h2hScoreViewR1A';
import H2hScoreViewR1B from '../H2H/score_views/h2hScoreViewR1B';
import H2hScoreViewR2 from '../H2H/score_views/h2hScoreViewR2';
import H2hScoreViewQF from '../H2H/score_views/h2hScoreViewQF';
import H2hScoreViewSF from '../H2H/score_views/h2hScoreViewSF';
import H2hScoreViewMB from '../H2H/score_views/h2hScoreViewMB';
import H2hScoreViewMG from '../H2H/score_views/h2hScoreViewMG';
import Homepage from '../homepage';

class NavBar extends React.Component {
    state = {
        activeItem: 'home'
    }
    render() {
        const { activeItem } = this.state.activeItem;
        const homepage = () => {return <Homepage />};
        const universityDashboard = () => { return <UniversityDashboard />; };
        const targetDashboard = () => { return <TargetListDashboard />; };
        const universityScores = () => { return <UniversityScores />; };
        const individualScores = () => { return <IndividualScores />; };
        const h2hTargetsR1A = () => { return <H2HTargetsR1A />; };
        const h2hTargetsR1B = () => { return <H2HTargetsR1B />; };
        const h2hTargetsR2 = () => { return <H2HTargetsR2 />; };
        const h2hTargetsQF = () => { return <H2HTargetsQF />; };
        const h2hTargetsSF = () => { return <H2HTargetsSF />; };
        const h2hTargetsMB = () => { return <H2HTargetsMB />; };
        const h2hTargetsMG = () => { return <H2HTargetsMG />; };
        const h2hScoreViewR1A = () => { return <H2hScoreViewR1A />; };
        const h2hScoreViewR1B = () => { return <H2hScoreViewR1B />; };
        const h2hScoreViewR2 = () => { return <H2hScoreViewR2 />; };
        const h2hScoreViewQF = () => { return <H2hScoreViewQF />; };
        const h2hScoreViewSF = () => { return <H2hScoreViewSF />; };
        const h2hScoreViewMB = () => { return <H2hScoreViewMB />; };
        const h2hScoreViewMG = () => { return <H2hScoreViewMG />; };


        return (
            <div>
                <Menu size='mini'>
                    <Menu.Item
                        name='homepage'
                        active={activeItem === 'Homepage'}
                        onClick={this.handleItemClick}
                        as={NavLink}
                        to='/'
                        
                    ><img src={logo} className="App-logo" alt="logo" /></Menu.Item>
                    {/* /> */}
                    <Menu.Item
                        name='registration'
                        active={activeItem === 'Registration'}
                        onClick={this.handleItemClick}
                        as={NavLink}
                        to='/registration'
                    />
                    <Menu.Item
                        name='targets'
                        active={activeItem === 'targets'}
                        onClick={this.handleItemClick}
                        as={NavLink}
                        to='/targets'
                    />
                    <Dropdown item simple text='Seeding Round'>
                        <Dropdown.Menu>
                            <Dropdown.Item
                                text='University Team Scores'
                                name='universityScores'
                                active={activeItem === 'universityScores'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/seeding_uni'
                            />
                            <Dropdown.Item
                                text='Individual Scores'
                                name='individualScores'
                                active={activeItem === 'individualScores'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/seeding_individual'
                            />
                        </Dropdown.Menu>
                    </Dropdown>
                    <Dropdown item simple text='H2H Targets'>
                        <Dropdown.Menu>

                            <Dropdown.Item
                                text='Round 1A'
                                name='h2h_target_list_R1A'
                                active={activeItem === 'h2hTargetsR1A'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/h2h_target_list_R1A'
                            />
                            <Dropdown.Item
                                text='Round 1B'
                                name='h2h_target_list_R1B'
                                active={activeItem === 'h2hTargetsR1B'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/h2h_target_list_R1B'
                            />
                            <Dropdown.Item
                                text='Round 2'
                                name='h2h_target_list_R2'
                                active={activeItem === 'h2hTargetsR2'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/h2h_target_list_R2'
                            />
                            <Dropdown.Item
                                text='Quarter Finals'
                                name='h2h_target_list_QF'
                                active={activeItem === 'h2hTargetsQF'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/h2h_target_list_QF'
                            />
                            <Dropdown.Item
                                text='Semi Finals'
                                name='h2h_target_list_SF'
                                active={activeItem === 'h2hTargetsSF'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/h2h_target_list_SF'
                            />
                            <Dropdown.Item
                                text='Bronze Medal Match'
                                name='h2h_target_list_MB'
                                active={activeItem === 'h2hTargetsMB'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/h2h_target_list_MB'
                            />
                            <Dropdown.Item
                                text='Gold Medal Match'
                                name='h2h_target_list_MG'
                                active={activeItem === 'h2hTargetsMG'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/h2h_target_list_MG'
                            />
                          
                        </Dropdown.Menu>

                    </Dropdown>
                    <Dropdown item simple text='H2H Scores'>
                        <Dropdown.Menu>
                            <Dropdown.Item
                                text='Round 1A'
                                name='h2h_scores_R1A'
                                active={activeItem === 'h2hScoresR1A'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/h2h_scoring_R1A'
                            />
                       
                            <Dropdown.Item
                                text='Round 1B'
                                name='h2h_scores_R1B'
                                active={activeItem === 'h2hScoresR1B'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/h2h_scoring_R1B'
                            />
                      
                            <Dropdown.Item
                                text='Round 2'
                                name='h2h_scores_R2'
                                active={activeItem === 'h2hScoresR2'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/h2h_scoring_R2'
                            />
                      
                            <Dropdown.Item
                                text='Quarter Finals'
                                name='h2h_scores_QF'
                                active={activeItem === 'h2hScoresQF'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/h2h_scoring_QF'
                            />
                      
                            <Dropdown.Item
                                text='Semi Finals'
                                name='h2h_scores_SF'
                                active={activeItem === 'h2hScoresSF'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/h2h_scoring_SF'
                            />
                      
                            <Dropdown.Item
                                text='Bronze Medal Match'
                                name='h2h_scores_MB'
                                active={activeItem === 'h2hScoresMB'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/h2h_scoring_MB'
                            />
                     
                            <Dropdown.Item
                                text='Gold Medal Match'
                                name='h2h_scores_MG'
                                active={activeItem === 'h2hScoresMG'}
                                onClick={this.handleItemClick}
                                as={NavLink}
                                to='/h2h_scoring_MG'
                            />
                        </Dropdown.Menu>

                    </Dropdown>
                </Menu>
                <Switch>
                    <Route exact path='/' component={homepage} />
                    <Route path='/registration/' component={universityDashboard} />
                    <Route path='/targets/' component={targetDashboard} />
                    <Route path='/seeding_uni/' component={universityScores} />
                    <Route path='/seeding_individual/' component={individualScores} />
                    <Route path='/h2h_target_list_R1A/' component={h2hTargetsR1A} />
                    <Route path='/h2h_target_list_R1B/' component={h2hTargetsR1B} />
                    <Route path='/h2h_target_list_R2/' component={h2hTargetsR2} />
                    <Route path='/h2h_target_list_QF/' component={h2hTargetsQF} />
                    <Route path='/h2h_target_list_SF/' component={h2hTargetsSF} />
                    <Route path='/h2h_target_list_MB/' component={h2hTargetsMB} />
                    <Route path='/h2h_target_list_MG/' component={h2hTargetsMG} />
                    <Route path='/h2h_scoring_R1A/' component={h2hScoreViewR1A} />
                    <Route path='/h2h_scoring_R1B/' component={h2hScoreViewR1B} />
                    <Route path='/h2h_scoring_R2/' component={h2hScoreViewR2} />
                    <Route path='/h2h_scoring_QF/' component={h2hScoreViewQF} />
                    <Route path='/h2h_scoring_SF/' component={h2hScoreViewSF} />
                    <Route path='/h2h_scoring_MB/' component={h2hScoreViewMB} />
                    <Route path='/h2h_scoring_MG/' component={h2hScoreViewMG} />
                </Switch>
            </div>
        );
    }
}

export default NavBar;